<template>
    <div id="root" >
        <v-container mt-4>
            <v-layout justify-center="">
                <v-flex xs12 sm11 md8  >
                    <v-card class="elevation-15" >
                        <v-container>
                            <v-layout justify-space-between="" row wrap>
                                <v-flex xs12 sm4 md6 >
                                    <!-- <img
                                        src="data:image/jpeg;base64,"
                                    > -->

                                    <img v-if="showIfspLogo" style="width:90%;"
                                        src="/assets/svg/ifsp-logo.svg"
                                    >

                                    <v-card v-if="!showIfspLogo" 
                                        style="width:35vmax; height:35vmax; border-radius: 50%; margin: auto auto;"
                                            class="elevation-10 pa-5 mb-2" 
                                    >
                                        <img 
                                            style="border-radius:0%;"
                                            src="/assets/svg/ifsp-logo.svg"
                                        >
                                    </v-card>
                                </v-flex>
                                <v-flex xs12 sm8 md6 >
                                    <v-form>
                                        <h2 class="font-weight-bold display-1">Login</h2>
                                        <h3 class="caption font-weight-regular">Acesso para servidores ou alunos</h3>
                                        <v-text-field  
                                            v-model="prontuary"
                                            :rules="prontuaryRules"
                                            label="Prontuário"
                                            required
                                        ></v-text-field>
                                        <v-text-field  
                                            v-model="password"
                                            :rules="passwordRules"
                                            label="Senha"
                                            type="password"
                                            required
                                        ></v-text-field>
                                        <v-btn
                                            @click="submit"
                                            class="primary"
                                        >
                                            Entrar
                                        </v-btn>
                                        <v-btn
                                            @click="submit"
                                            class=""
                                            flat=""
                                        >
                                            Recuperar Senha
                                        </v-btn>

                                    </v-form>
                                </v-flex>
                            </v-layout>
                        </v-container>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>        
    </div>
</template>

<script>
export default {
  data: function() {
    return {
      prontuary: "",
      prontuaryRules: [],
      password: "",
      passwordRules: []
    };
  },
  computed: {
    showIfspLogo() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return false;
        case "sm":
        case "md":
        case "lg":
        case "xl":
          return true;
      }
    }
  },
  methods: {
      submit: function (){
          this.$router.push({ name: "student"})
      }
  }
};
</script>



<style lang="scss">
#root {
  & {
    width: 100%;
    height: 100%;
    // background-image: url("https://brasilescola.uol.com.br/upload/vestibular/-593abbd89ee9c.png")
  }
}
</style>

